import { Logo } from '../../core/ui';
import './index.less';
const Testimonial = () => {

    return (
        <section id="testimonial" className="testimonial">
            <div className="overlay padding-120">
                <div className="container" style={{padding:'20px'}}>
                    <div className="section-header text-center">
                        <h3>关于我们</h3>
                        <p>
                            <i>
                                技术是美好的，如果再能够遇到一群有趣的人，那就...哈哈哈...
                            </i>
                        </p>
                    </div>

                    <div className="testimonial-slider swiper-container swiper-container-horizontal">
                        <div className="swiper-wrapper" style={{ transform: 'translate3d(-2280px, 0px, 0px)', transitionDuration: '0ms' }}>
                            <div className="swiper-slide swiper-slide-duplicate swiper-slide-duplicate-next" data-swiper-slide-index="2" style={{ width: '1140px' }}>
                                <div className="testimonial-item">
                                    <div className="image">
                                        <img src={Logo} alt="client image" className="img-responsive" />
                                    </div>
                                    <div className="content">
                                        <h4></h4>
                                        <span></span>
                                        <p><em></em></p>
                                    </div>
                                </div>
                            </div>
                            <div className="swiper-slide swiper-slide-prev" data-swiper-slide-index="0" style={{ width: '1140px' }}>
                                <div className="testimonial-item">
                                    <div className="image">
                                        <img src={Logo} alt="client image" className="img-responsive" />
                                    </div>
                                    <div className="content">
                                        <h4></h4>
                                        <span></span>
                                        <p><em></em></p>
                                    </div>
                                </div>
                            </div>

                            <div className="swiper-slide swiper-slide-duplicate swiper-slide-duplicate-prev" data-swiper-slide-index="0" style={{ width: '1140px' }}>
                                <div className="testimonial-item">
                                    <div className="image">
                                        <img src={Logo} alt="client image" className="img-responsive" />
                                    </div>
                                    <div className="content">
                                        <h4>GFE TEAM</h4>
                                        {/* <span>Doctor</span> */}
                                        <span></span>
                                        <p><em>"我们着力打造完善的技术体系、通过技术创造价值、结合新技术开发各种有趣的开源项目 ！"</em></p>
                                        <p><em>"我们提倡劳逸结合，展现自我, 做事专业，严谨，乐于分享, 希望每个人都能够快乐工作，认真生活 ！"</em></p>
                                        <p><em>"未来我们也希望通过聚合业界前端力量，来共同为推动 Web 前端技术的发展贡献一点力量 ！"</em></p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {/* <div className="testimonial-pagination swiper-pagination-clickable swiper-pagination-bullets">
                            <span className="swiper-pagination-bullet"></span>
                            <span className="swiper-pagination-bullet swiper-pagination-bullet-active"></span>
                            <span className="swiper-pagination-bullet"></span>
                        </div> */}
                    </div>
                </div>
            </div>
        </section>
    );
}

export default Testimonial;
